import {min0max999999} from '../utils/validateForms';

export const types = ['', '', '', '', '', '', '', '', '', '', '', '', ''] as const
export const worldViews = ['-', '-', '-', '-', '', '-', '-', '-', '-'] as const
export const sizes = ['', '', '', '', '', ''] as const

export type TType = typeof types[number]
export type TWorldView = typeof worldViews[number]
export type TSize = typeof sizes[number]

type TStat = 'strength' | 'agility' | 'constitution' | 'intelligence' | 'wisdom' | 'charisma'
type TSkill = 'athletics'
    | 'acrobatics'
    | 'sleightOfHand'
    | 'stealth'
    | 'arcana'
    | 'history'
    | 'investigation'
    | 'nature'
    | 'religion'
    | 'animalHandling'
    | 'insight'
    | 'medicine'
    | 'perception'
    | 'survival'
    | 'deception'
    | 'intimidation'
    | 'performance'
    | 'persuasion'

interface IStatData {
    displayName: string
    inputName: TStat
}

interface ISaveThrowStatData {
    displayName: string
    inputName: 'strengthSaveThrow' | 'agilitySaveThrow' | 'constitutionSaveThrow' | 'intelligenceSaveThrow' | 'wisdomSaveThrow' | 'charismaSaveThrow'
}

interface IInputData {
    validateFunction: Function
    asideValue: string
}

interface IVisionData extends IInputData {
    visionNames: { displayName: string, inputName: 'vision' | 'visionDark' | 'visionBlind' | 'visionTrue' }[]
}

interface ISpeedData extends IInputData {
    speedNames: { displayName: string, inputName: 'speed' | 'speedClimb' | 'speedSwim' | 'speedFly' }[]
}

interface ISkillData {
    inputName: TSkill
    displayName: string
    stat: TStat
}

export const statsData: IStatData[] = [
    {
        displayName: '',
        inputName: 'strength'
    },
    {
        displayName: '',
        inputName: 'agility'
    },
    {
        displayName: '',
        inputName: 'constitution'
    },
    {
        displayName: '',
        inputName: 'intelligence'
    },
    {
        displayName: '',
        inputName: 'wisdom'
    },
    {
        displayName: '',
        inputName: 'charisma'
    }
]

export const saveThrowsStatsData = statsData.map(statData => ({
    ...statData,
    inputName: statData.inputName + 'SaveThrow'
})) as ISaveThrowStatData[]

export const speedData: ISpeedData = {
    speedNames: [
        {displayName: '', inputName: 'speed'},
        {displayName: '', inputName: 'speedClimb'},
        {displayName: '', inputName: 'speedSwim'},
        {displayName: '', inputName: 'speedFly'}
    ],
    validateFunction: min0max999999,
    asideValue: '()'
}

export const visionData: IVisionData = {
    visionNames: [
        {displayName: '', inputName: 'vision'},
        {displayName: '', inputName: 'visionBlind'},
        {displayName: '', inputName: 'visionDark'},
        {displayName: '', inputName: 'visionTrue'}
    ],
    asideValue: '()',
    validateFunction: min0max999999
}

export const skillsData: ISkillData[] = [
    {inputName: 'athletics', displayName: '', stat: 'strength'},

    {inputName: 'acrobatics', displayName: '', stat: 'agility'},
    {inputName: 'sleightOfHand', displayName: ' ', stat: 'agility'},
    {inputName: 'stealth', displayName: '', stat: 'agility'},

    {inputName: 'arcana', displayName: '', stat: 'intelligence'},
    {inputName: 'history', displayName: '', stat: 'intelligence'},
    {inputName: 'investigation', displayName: '', stat: 'intelligence'},
    {inputName: 'nature', displayName: '', stat: 'intelligence'},
    {inputName: 'religion', displayName: '', stat: 'intelligence'},

    {inputName: 'animalHandling', displayName: '  ', stat: 'wisdom'},
    {inputName: 'insight', displayName: '', stat: 'wisdom'},
    {inputName: 'medicine', displayName: '', stat: 'wisdom'},
    {inputName: 'perception', displayName: '', stat: 'wisdom'},
    {inputName: 'survival', displayName: '', stat: 'wisdom'},

    {inputName: 'deception', displayName: '', stat: 'charisma'},
    {inputName: 'intimidation', displayName: '', stat: 'charisma'},
    {inputName: 'performance', displayName: '', stat: 'charisma'},
    {inputName: 'persuasion', displayName: '', stat: 'charisma'}
]

