import {ICreatureData} from '../types/creatureTypes';
import {addPlusToPositive} from './stringUtils';

export function statToModifier(stat: string) {
    return Math.floor((+stat - 10) / 2)
}

export function getCreatureStats({strength, agility, constitution, intelligence, wisdom, charisma}: ICreatureData) {
    return [{name: '', value: strength}, {name: '', value: agility}, {
        name: '',
        value: constitution
    }, {name: '', value: intelligence}, {name: '', value: wisdom}, {name: '', value: charisma}]
}

export function formHeadDescStr({size, type, worldview}: ICreatureData) {
    return `${size[0].toUpperCase() + size.slice(1)}, ${type}, ${worldview}`
}

export const formSpeedStr = ({speed, speedClimb, speedFly, speedSwim}: ICreatureData) => {
    return `${speed} `
        + (+speedClimb > 0 ? `,  ${speedClimb} ` : '')
        + (+speedFly > 0 ? `,  ${speedFly} ` : '')
        + (+speedSwim > 0 ? `,  ${speedSwim} ` : '')
}

export const formVisionStr = ({vision, visionBlind, visionDark, visionTrue}: ICreatureData) => {
    return ` ${vision} `
        + (+visionBlind > 0 ? `,  ${visionBlind} ` : '')
        + (+visionDark > 0 ? `,  ${visionDark} ` : '')
        + (+visionTrue > 0 ? `,   ${visionTrue} ` : '')
}

export const formSaveThrowStr = ({
                                     strengthSaveThrow,
                                     agilitySaveThrow,
                                     constitutionSaveThrow,
                                     intelligenceSaveThrow,
                                     wisdomSaveThrow,
                                     charismaSaveThrow
                                 }: ICreatureData) => {
    return (strengthSaveThrow ? ` ${addPlusToPositive(+strengthSaveThrow)}` : '') +
        (agilitySaveThrow ? ` ${addPlusToPositive(+agilitySaveThrow)}` : '') +
        (constitutionSaveThrow ? ` ${addPlusToPositive(+constitutionSaveThrow)}` : '') +
        (intelligenceSaveThrow ? ` ${addPlusToPositive(+intelligenceSaveThrow)}` : '') +
        (wisdomSaveThrow ? ` ${addPlusToPositive(+wisdomSaveThrow)}` : '') +
        (charismaSaveThrow ? ` ${addPlusToPositive(+charismaSaveThrow)}` : '')
}

export const isThereSaveThrows = ({
                                      strengthSaveThrow,
                                      agilitySaveThrow,
                                      constitutionSaveThrow,
                                      intelligenceSaveThrow,
                                      wisdomSaveThrow,
                                      charismaSaveThrow
                                  }: ICreatureData) => {
    return [strengthSaveThrow, agilitySaveThrow, constitutionSaveThrow, intelligenceSaveThrow, wisdomSaveThrow, charismaSaveThrow].some(saveThrow => saveThrow)
}